import { useNavigate } from 'react-router-dom';
import { Formik, Form } from 'formik';
import { makeStyles } from '@mui/styles';
import { Box, Container } from '@mui/material';

import { FORMDATA, SELECTORS } from './formdata';
import { Input, Selector } from '../../atoms';
import { FlowOptions } from '../FlowOptions';
import { createUser } from '../../../../api/user';
import { userValidate } from '../../../../validate/userReg';

import './Editform.css';

const useStyles = makeStyles((theme) => ({
  button: {
    backgroundColor: 'red',
  },
  box: {
    mt: 1,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  field: {
    width: '100%',
    marginBottom: '20px',
    '&:last-child': {
      marginBottom: 0,
    },
  },
}));

export const EditForm = (props) => {
  const navigate = useNavigate();
  const classes = useStyles();

  const handleCancelBtn = () => {
    navigate('/login');
  };

  return (
    <>
      <Container component='main' maxWidth='xs'>
        <Box className='register-form-box'>
          <Formik
            initialValues={{
              firstName: '',
              secondName: '',
              lastName: '',
              email: '',
              phone: '',
              address: '',
              password: '',
              country: '',
              city: '',
            }}
            onSubmit={(values) => {
              const user = {
                first_name: values.firstName,
                second_name: values.secondName,
                last_name: values.lastName,
                ...values,
              };
              createUser(user)
                .then((res) => {
                  console.log(res.data);
                  navigate('/');
                })
                .catch((error) => console.error(error));
            }}
            validationSchema={userValidate}
          >
            {({ handleChange, isSubmitting }) => (
              <Form className={classes.box}>
                {FORMDATA.map((data, index) => (
                  <Input {...data} key={index} />
                ))}
                {SELECTORS.map((selector, index) => {
                  return <Selector {...selector} handleChange={handleChange} />;
                })}
                <Input name='city' label='City' type='text' />

                <FlowOptions
                  btn1_title='cancel'
                  btn1onClick={handleCancelBtn}
                  btn2_title='continue'
                  btn2Disabled={isSubmitting}
                  btn2_variant='contained'
                />
              </Form>
            )}
          </Formik>
        </Box>
      </Container>
    </>
  );
};
